/* Dropdown.css */
.dropdown-menu {
  background: red;
  width: 200px;
  position: absolute;
  top: 80px;
  list-style: none;
  text-align: start;
}

.dropdown-menu li {
  background: #1888ff;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background: #5cabff;
}

.dropdown-menu.clicked {
  display: none;
}

.dropdown-link {
  display: block;
  height: 100%;
  width: 100%;
  text-decoration: none;
  color: #fff;
  padding: 16px;
}

@media screen and (max-width: 960px) {
  .fa-caret-down {
    height: 4.1em;
    width: 16.5%;
    background-color: black;
    color: #fff;
    display: flex;
    align-items: center;
    margin-right: auto;
    justify-content: center;
  }

  .dropdown-menu {
    position: initial;
    display: block;
    width: 80%;
    background: red;
    list-style: none;
    text-align: start;
    margin: auto;
    z-index: 1;
  }

  .dropdown-menu li {
    background: #1888ff;
    cursor: pointer;
  }

  .dropdown-menu li:hover {
    background: #5cabff;
  }

  .dropdown-menu.clicked {
    display: none;
  }

  .dropdown-link {
    display: block;
    height: 100%;
    width: 100%;
    text-decoration: none;
    color: #fff;
    padding: 16px;
  }
}
