* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}
h1{
  color: white;
}
.home,
.Projects,
.Netapi,
.Documents,
.Fronttech {
  align-items: center;
  justify-content: center;
  margin-top: 80px;
}
.home,
.Projects,
.Netapi,
.Fronttech,
.Documents,
.aboutme,
.contact,
.SMTrend {
  position: relative; /* Add this to position content elements */
  z-index: 2; /* Add this to give content a higher z-index */
}

.backwrap {
  background: #113243;
  /* Old browsers */
  background-image: url("data:image/png;base64,");
  background-repeat: no-repeat;
  background-size: 100% 100%;
  width: 100%;
  height: 100%;
  position: fixed;
  left: 0;
  top: 0;
  z-index: 1;
}

/* Shapes & Animation
*/
.back-shapes {
  width: 100%;
  height: 100%;
  position: relative;
  overflow: hidden;
}

.back-shapes img {
  opacity: 0.2;
  position: absolute;
  width: 1.5%;
}

.floating {
  position: absolute;
  animation-name: floating;
  -webkit-animation-name: floating;
  animation-duration: 5s;
  -webkit-animation-duration: 5s;
  animation-iteration-count: infinite;
  -webkit-animation-iteration-count: infinite;
}
.floating.circle {
  display: inline-block;
  width: 39px;
  height: 39px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.5;
}
.floating.square {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.5;
}
.floating.triangle {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.5;
}
.floating.cross {
  display: inline-block;
  width: 35px;
  height: 35px;
  background-image: url("data:image/png;base64,");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  opacity: 0.5;
}

@keyframes floating {
  0% {
    transform: translateY(0%) rotate(-55deg);
  }
  50% {
    transform: translateY(300%) rotate(55deg);
  }
  100% {
    transform: translateY(0%) rotate(-55deg);
  }
}
@-webkit-keyframes floating {
  0% {
    -webkit-transform: translateY(0%);
  }
  50% {
    -webkit-transform: translateY(300%);
  }
  100% {
    -webkit-transform: translateY(0%);
  }
}
