.contact {
  text-align: center;
  font-size: 20px;
}

.contact__title {
  font-size: 50px;
  color: #ffffff;
}

.contact p {
  color: red;
}

.contact-form {
  max-width: 800px;
  margin: 0 auto;
  padding: 20px;
  border-radius: 5px;
  background: radial-gradient(circle, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 1) 100%);
  box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.5);


}

.contact-form__form {
  display: flex;
  flex-direction: column;
  color: rgb(255, 255, 255);
}

.contact-form__form-group {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 20px;
  align-items: flex-end;
}

.contact-form__half {
  width: 46%;
  margin: 0 2% 0 2%;
}

.contact-form__label {
  font-weight: bold;
  font-size: 20px;
  display: block;
  margin-bottom: 10px;
  color: #ffffff;
}

.contact-form__input,
.contact-form__textarea,
.contact-form__button {
  width: 100%;
  padding: 10px;
  border: 2px solid #000;
  border-radius: 5px;
  margin-bottom: 10px;
}

.contact-form__textarea {
  height: auto;
}

.contact-form__button {
  background-color: #007bff;
  color: #fff;
  border: none;
  padding: 10px 0;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-top: 20px;
}

.contact-form__button:hover {
  background-color: #0056b3;
}

.contact-form__button:disabled {
  background-color: #cccccc;
  cursor: not-allowed;
}
